<template>
  <div class="login bgfff ls1">
    <div class="header">
      <div class="backClose">
        <img
          class="back"
          src="../../images/fan-hui.png"
          @click="$router.replace('/login/PwdLogin')"
          alt
        >
      </div>
      <p class="ellipsis">找回密码</p>
      <div class="colSahare"></div>
    </div>
    <div class="pl40 pr40">
      <div class="item bor_b">
        <input
          type="tel"
          v-model="phone"
          @focus="close=true"
          @blur="close=false"
          maxlength="13"
          @keyup="telNum($event)"
          placeholder="请输入手机号"
        >
        <p>
          <img src="../../images/qing-chu.png" class="close" v-show="close" @click="phone=''" alt>
        </p>
      </div>
      <div class="item bor_b">
        <input
          type="text"
          v-model="code"
          :readonly="readonly_"
          placeholder="请输入验证码"
          maxlength="6"
        >
        <span class="get_code" :class="{on: phone}" @click="getCode" v-text="getCodeText"></span>
      </div>
      <div class="item bor_b">
        <input
          :type="passwordType"
          v-model="password"
          @focus="close2=true"
          @blur="close2=false"
          placeholder="请输入新密码（最少6位）"
        >
        <p>
          <img
            src="../../images/qing-chu.png"
            class="close left"
            v-show="close2"
            @click="password=null"
            alt
          >
          <img
            src="../../images/eye2.png"
            class="eye"
            v-show="!eye"
            @click="eye_(true)"
            alt
          >
          <img src="../../images/eye1.png" class="eye" v-show="eye" @click="eye_(false)" alt>
        </p>
      </div>
      <p class="login_btn" :class="{on: phone&&code&&password}" @click="login">确定</p>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Header";
import { phoneNumber } from "@/utils/check";
import secret from "@/utils/secret.js";
export default {
  components: {
    Header
  },
  data() {
    return {
      phone: "",
      code: null,
      readonly_: false, // 验证码默认禁止输入
      password: null,
      passwordType: "password",
      close: false, // 清除按钮
      close2: false, // 清除按钮
      eye: false, // 密码可见
      protocol: true, // 协议勾选状态
      isGetCode: true, // 获取验证码按钮防止重复点击
      getCodeText: "获取验证码",
      click_btn: true
    };
  },
  watch: {
    showHeight: "inputType",
    phone(newValue, oldValue) {
      // 具体的代码在这里实现
      this.phone =
        newValue.length > oldValue.length
          ? newValue
              .replace(/\s/g, "")
              .replace(/(\d{3})(\d{0,4})(\d{0,4})/, "$1 $2 $3")
          : this.phone.trim();
    }
  },
  methods: {
    // 输入带空格的手机号
    telNum(e) {
      let obj = e.target;
      if (e.keyCode == 8) return;
      let value = obj.value;
      value = value.replace(/\s*/g, "");
      let result = [];
      for (let i = 0; i < value.length; i++) {
        if (i == 3 || i == 7) {
          result.push(" " + value.charAt(i));
        } else {
          result.push(value.charAt(i));
        }
      }
      obj.value = result.join("");
    },
    // 获取验证码（加密接口）
    getCode() {
      let ph = this.phone;
      ph = ph.replace(/\s*/g, "");
      if (phoneNumber(ph)) {
        const params = {
          phone: secret.encrypt(ph), // 加密
          msg_sign: 0, //  
          verify_type: "retrieve" // register=>注册 , verify=>快捷登录 , retrieve=>找回密码
        };
        if (this.isGetCode) {
          this.isGetCode = false;
          this.fetch.aesGetCode(params, res => {
            let _time = 60;
            let $this = this;
            if (res.code === 200) {
              this.readonly_ = false;
              $this.getCodeText = `(${_time}s)重新获取`;
              const setTime = setInterval(() => {
                _time = _time - 1;
                $this.getCodeText = `(${_time}s)重新获取`;
                if (_time === 0) {
                  clearInterval(setTime);
                  this.isGetCode = true;
                  $this.getCodeText = "重新获取";
                }
              }, 1e3);
            } else {
              this.isGetCode = true;
              let time = 2e3;
              this.$toast(res.info, time, 1);
            }
          });
        }
      } else {
        let time = 2e3;
        this.throttle(() => {
          // 信息提示
          this.$toast("请输入正确的手机号", time, 1);
        }, time);
      }
    },
   
    // 找回密码（加密接口）
    login() {
      if (!this.click_btn) {
        return;
      }
      this.click_btn = false;
      setTimeout(() => {
        this.click_btn = true;
      }, 500);
      let ph = this.phone;
      ph = ph.replace(/\s*/g, "");
      if (ph && this.code && this.password) {
        if (this.password.length > 5) {
          const params = {
            phone: secret.encrypt(ph), // 加密
            password: secret.encrypt(this.password), // 加密
            verification: this.code, // 验证码
            verify_type: "retrieve" // 验证码类型 : register=>注册 , verify=>快捷登录 , retrieve=>找回密码
          };
          this.fetch.aesretrievePwd(params, res => {
            setTimeout(() => {
              this.click_btn = true;
            }, 500);
            if (res.code === 200) {
              let time = 1e3;
              this.throttle(() => {
                // 信息提示
                this.$toast(res.info, time, 2);
              }, time);
              setTimeout(() => {
                this.$router.replace("/login/PwdLogin");
              }, 500);
            } else {
              let time = 2e3;
              this.throttle(() => {
                // 信息提示
                this.$toast(res.info, time, 1);
              }, time);
            }
          });
        } else {
          let time = 2e3;
          this.throttle(() => {
            // 信息提示
            this.$toast("密码不能少于6位", time, 1);
          }, time);
        }
      }
    },
    // 获取验证码
    // getCode() {
    //   let ph = this.phone;
    //   ph = ph.replace(/\s*/g, "");
    //   if (phoneNumber(ph)) {
    //     const params = {
    //       phone: ph,
    //       msg_sign: 0, //  
    //       verify_type: "retrieve" // register=>注册 , verify=>快捷登录 , retrieve=>找回密码
    //     };
    //     if (this.isGetCode) {
    //       this.isGetCode = false;
    //       this.fetch.getCode(params, res => {
    //         let _time = 60;
    //         let $this = this;
    //         if (res.code === 200) {
    //           this.readonly_ = false;
    //           $this.getCodeText = `(${_time}s)重新获取`;
    //           const setTime = setInterval(() => {
    //             _time = _time - 1;
    //             $this.getCodeText = `(${_time}s)重新获取`;
    //             if (_time === 0) {
    //               clearInterval(setTime);
    //               this.isGetCode = true;
    //               $this.getCodeText = "重新获取";
    //             }
    //           }, 1e3);
    //         } else {
    //           this.isGetCode = true;
    //           let time = 2e3;
    //           this.throttle(() => {
    //             // 信息提示
    //             this.$toast(res.info, time, 1);
    //           }, time);
    //         }
    //       });
    //     }
    //   } else {
    //     let time = 2e3;
    //     this.throttle(() => {
    //       // 信息提示
    //       this.$toast("请输入正确的手机号", time, 1);
    //     }, time);
    //   }
    // },
    
    // 找回密码
    // login() {
    //   if (!this.click_btn) {
    //     return;
    //   }
    //   this.click_btn = false;
    //   setTimeout(() => {
    //     this.click_btn = true;
    //   }, 1000);
    //   let ph = this.phone;
    //   ph = ph.replace(/\s*/g, "");
    //   if (ph && this.code && this.password) {
    //     if (this.password.length > 5) {
    //       const params = {
    //         phone: ph,
    //         password: this.password,
    //         verification: this.code, // 验证码
    //         verify_type: "retrieve" // 验证码类型 : register=>注册 , verify=>快捷登录 , retrieve=>找回密码
    //       };
    //       this.fetch.retrievePwd(params, res => {
    //         setTimeout(() => {
    //           this.click_btn = true;
    //         }, 1000);
    //         if (res.code === 200) {
    //           let time = 1e3;
    //           this.throttle(() => {
    //             // 信息提示
    //             this.$toast(res.info, time, 2);
    //           }, time);
    //           setTimeout(() => {
    //             this.$router.replace("/login/PwdLogin");
    //           }, 1e3);
    //         } else {
    //           let time = 2e3;
    //           this.throttle(() => {
    //             // 信息提示
    //             this.$toast(res.info, time, 1);
    //           }, time);
    //         }
    //       });
    //     } else {
    //       let time = 2e3;
    //       this.throttle(() => {
    //         // 信息提示
    //         this.$toast("密码不能少于6位", time, 1);
    //       }, time);
    //     }
    //   }
    // },
    eye_(type_) {
      // 密码可见
      type_ ? (this.passwordType = "text") : (this.passwordType = "password");
      this.eye = type_;
    }
  }
};
</script>
<style lang="scss" scoped>
.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
  height: 0.88rem;
  line-height: 0.88rem;
  background-color: #fff;
  border-bottom: 0.01rem solid #edeff5;
  box-sizing: border-box;
  padding: 0 0.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .ellipsis {
    // 标题
    width: 60%;
    box-sizing: border-box;
    font-size: 0.36rem;
    letter-spacing: 0.01rem;
    color: #101d37;
    font-weight: bold;
    text-align: center;
  }
  .colSahare {
    // 分享收藏
    margin-top: 0.1rem;
    width: 20%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-sizing: border-box;
    padding-left: 0.2rem;
    .iconCollect {
      width: 0.8rem;
      margin-left: 0.8rem;
      // margin-right: 1rem;
      img {
        width: 0.32rem;
      }
    }
    .agree {
      letter-spacing: 0.01rem;
      color: #2e6eec;
      font-size: 0.3rem;
    }
  }
  .backClose {
    // 返回
    width: 20%;
    box-sizing: border-box;
    margin-top: 0.1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding-right: 0.5rem;
    .back {
      width: 0.2rem;
      height: 0.34rem;
      margin-bottom: 0.14rem;
      margin-right: 0.4rem;
    }
    .close {
      width: 0.32rem;
      margin-bottom: 0.12rem;
    }
  }
}
.login {
  .header {
    border: none;
  }
}
</style>



